'use client'

import { useEffect, useState } from 'react'
import Link from 'next/link'

type Product = {
  p_id: number
  name: string
  description: string
  price: number
  category?: {
    name: string
  }
}

export default function ProductListPage() {
  const [products, setProducts] = useState<Product[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchProducts = async () => {
      setLoading(true)
      setError(null)
      try {
        const res = await fetch('/api/products')
        if (!res.ok) throw new Error('ไม่สามารถดึงข้อมูลสินค้าได้')
        const data = await res.json()
        setProducts(data)
      } catch (err: any) {
        setError(err.message || 'เกิดข้อผิดพลาดในการดึงข้อมูล')
      } finally {
        setLoading(false)
      }
    }
    fetchProducts()
  }, [])

  return (
    <div>
      {/* Navbar */}
      <nav className="bg-gray-800 text-white p-4 flex justify-between items-center">
        <h1 className="text-xl font-bold">ระบบจัดการสินค้า</h1>
        <div className="space-x-4">
          <Link href="/">
            <button className="hover:underline">หน้าหลัก</button>
          </Link>
          <Link href="/categories/new">
            <button className="bg-yellow-500 px-3 py-1 rounded hover:bg-yellow-600">
              ➕ เพิ่มหมวดหมู่
            </button>
          </Link>
          <Link href="/products/new">
            <button className="bg-green-600 px-3 py-1 rounded hover:bg-green-700">
              ➕ เพิ่มสินค้า
            </button>
          </Link>
        </div>
      </nav>

      {/* Main content */}
      <div className="p-6 max-w-4xl mx-auto">
        <h2 className="text-2xl font-bold mb-4">รายการสินค้า</h2>

        {loading && <p className="text-center">กำลังโหลดข้อมูลสินค้า...</p>}
        {error && <p className="text-center text-red-600">เกิดข้อผิดพลาด: {error}</p>}

        {!loading && !error && (
          <>
            {products.length === 0 ? (
              <p>ไม่มีสินค้าที่จะแสดง</p>
            ) : (
              <ul className="space-y-4">
                {products.map((product) => (
                  <li
                    key={product.p_id}
                    className="p-4 border rounded shadow bg-white hover:shadow-lg transition"
                  >
                    <h3 className="text-lg font-semibold">{product.name}</h3>
                    <p className="text-sm text-gray-600 mb-1">{product.description}</p>
                    <p className="text-md text-green-700 font-bold">ราคา: {product.price} บาท</p>
                    <p className="text-sm text-blue-500">
                      หมวดหมู่: {product.category?.name || 'ไม่ระบุหมวดหมู่'}
                    </p>
                  </li>
                ))}
              </ul>
            )}
          </>
        )}
      </div>
    </div>
  )
}
